.lightbox {
  position: fixed;
  top: 50% !important;
  transform: translateY(-50%);
}
.lb-dataContainer {
  position: absolute;
  top: -40px;
  right: 50%;
  transform: translateX(50%);
}
.agrandar-img {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: $blue-claro;
}